// CryptoJS
var Crypto = require('./cryptojs.js').Crypto;
function RdWXBizDataCrypt(sessionKey) {
  this.sessionKey = sessionKey
}

RdWXBizDataCrypt.prototype.decryptData = function (encryptedData, iv) {
  // base64 decode  CryptoJS  Crypto.util.base64ToBytes() base64
  var encryptedData = Crypto.util.base64ToBytes(encryptedData)
  var key = Crypto.util.base64ToBytes(this.sessionKey);
  var iv = Crypto.util.base64ToBytes(iv);
  //  AES-128-CBCPKCS#7
  var mode = new Crypto.mode.CBC(Crypto.pad.pkcs7);
  try {
    // 
    var bytes = Crypto.AES.decrypt(encryptedData, key, {
      //asBpytes: true,
      iv: iv,
      mode: mode
    });

   // var decryptResult = JSON.parse(bytes);
  } catch (err) {
    console.log(err)
  }
  //
  //if (decryptResult.watermark.appid !== this.appId) {
  //  console.log(err)
  // }
  return bytes
}

RdWXBizDataCrypt.prototype.encryptData = function (contentData, iv) {
  var key = Crypto.util.base64ToBytes(this.sessionKey);
  var iv = Crypto.util.base64ToBytes(iv);
  //  AES-128-CBCPKCS#7
  var mode = new Crypto.mode.CBC(Crypto.pad.pkcs7);
  try {
    // 
    var bytes = Crypto.AES.encrypt(contentData, key, {
      //asBpytes: true,
      iv: iv,
      mode: mode
    });
    return bytes
  } catch (err) {
    console.log(err)
  }
  return null
}

module.exports = RdWXBizDataCrypt